import { Inject, Injectable } from '@angular/core';
import { WINDOW } from './window-token.module';
import { DOCUMENT } from '@angular/common';

interface Handler {
  element: HTMLElement;
  listener: any;
}

@Injectable()
export class NgxInViewportService {
  private handlers: Handler[] = [];

  constructor(
    @Inject(WINDOW) private window: any,
    @Inject(DOCUMENT) private document: any,
  ) { }

  addDetectListener(element: HTMLElement, inViewportHandler: () => void) {
    if (!this.window || !element || !inViewportHandler) return;

    const handler: Handler = {
      listener: () => this.elementOnViewport(element, inViewportHandler),
      element: element,
    };

    this.elementOnViewport(element, inViewportHandler); // need when page has been loaded
    this.window.addEventListener('scroll', handler.listener);

    this.handlers.push(handler);
  }

  removeDetectListener(element: HTMLElement) {
    const handler = this.handlers.find(h => h.element === element);
    this.window.removeEventListener('scroll', handler.listener);
  }

  private elementOnViewport(element: HTMLElement, inViewportHandler: () => void) {
    if (this.isElementOnViewport(element)) inViewportHandler();
  }

  private isElementOnViewport(element: HTMLElement): boolean {
    if (!this.window || !this.document) return;

    const clientRect: ClientRect = element.getBoundingClientRect();
    return !!clientRect &&
      clientRect.top <= this.document.body.clientHeight &&
      clientRect.left <= this.document.body.clientWidth &&
      clientRect.bottom >= 0 &&
      clientRect.right >= 0;
  }
}
