import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InViewportDirective } from './ngx-in-viewport.directive';
import { NgxInViewportService } from './ngx-in-viewport.service';
import { WindowTokenModule } from './window-token.module';


@NgModule({
  imports: [
    CommonModule,
    WindowTokenModule,
  ],
  declarations: [
    InViewportDirective,
  ],
  exports: [
    InViewportDirective,
  ],
})
export class InViewportModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: InViewportModule,
      providers: [
        NgxInViewportService,
      ],
    };
  }
}
