import { AfterViewInit, Directive, ElementRef, EventEmitter, OnDestroy, Output } from '@angular/core';
import { NgxInViewportService } from './ngx-in-viewport.service';

@Directive({
  // tslint:disable-next-line:directive-selector
  selector: '[in-viewport]'
})
export class InViewportDirective implements AfterViewInit, OnDestroy {
  @Output() inViewportAction = new EventEmitter();

  constructor(private inViewportService: NgxInViewportService, private element: ElementRef) { }

  ngAfterViewInit() {
    this.inViewportService
      .addDetectListener(this.element.nativeElement, () => this.inViewportAction.emit());
  }

  ngOnDestroy() {
    this.inViewportService.removeDetectListener(this.element.nativeElement);
  }
}
